* {
  margin-block-start: 0;
  margin-block-end: 0;
  scroll-behavior: smooth;
}

html {
  font-family: $mainFontFamily;
  font-size: 16px;
  line-height: 1.7em;
  scroll-behavior: smooth;
  width: 100vw;
  overflow-x: hidden;
}

.active {
  display: block;
}

.none {
  display: none;
}
